.wrapper {
  padding-left: 18px;
  padding-right: 18px;
  overflow: hidden;
  max-width: 1236px;
  margin-left: auto;
  margin-right: auto;
  &--medium {
    max-width: 976px
  }
  .wrapper {
    padding-left: 0;
    padding-right: 0;
  }
  &--b-margin {
    margin-bottom: 1rem;
    @include atSmall {
      &--b-margin {
        margin-bottom: 3.875rem;
      }
    }
  }

}
