.btn {
  background-color: $mainBlueColor;
  color: #fff;
  text-decoration: none;
  padding: 0.75rem 1.2rem;
  display: inline-block;
  &--orange {
    background-color: $mainOrangeColor;
  }
  &--large {
    padding: 1 .1rem 1.9rem;
    @include atSmall {
      font-size: 1.25rem;
    }
  }
}
